.Npmjs {
  overflow: hidden;
}

.Npmjs-Scroll {
  display: -ms-flexbox;
  display: flex;
  -webkit-transition: 1s all;
  -o-transition: 1s all;
  transition: 1s all;
}

.Npmjs-Page {
  background-color: black;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: calc(100vh - 160px);
  width: 100vw;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  font-size: 40px;
  -ms-flex-align: center;
      align-items: center;
  padding: 80px 0;
  position: relative;
}

.Npmjs-Page--title {
  font-size: 60px;
}

.Npmjs-Page--number {
  position: absolute;
  bottom: 10px;
  right: 20px;
  font-size: 20px;
}

.Npmjs-Page--image {
  width: 450px;
  margin-left: 100px;
}

code {
  background-color: white;
  color: black;
}

.Npmjs-Page--content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 100px;
}

.Npmjs-Page--text {
  margin-bottom: 10px;
}



